<template>
  <div class="comments">
    <h2>Comments</h2>
    <div v-for="comment in comments" :key="comment.id" class="comment">
      <p>{{ comment.text }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: ["id"],
  data() {
    return {
      comments: [],
    };
  },
  mounted() {
    this.fetchComments();
  },
  methods: {
    fetchComments() {
      axios
        .get(
          `https://hacker-news.firebaseio.com/v0/item/${this.id}.json?print=pretty`
        )
        .then((response) => {
          const commentIds = response.data.kids || [];
          const commentPromises = commentIds.map((id) =>
            axios.get(
              `https://hacker-news.firebaseio.com/v0/item/${id}.json?print=pretty`
            )
          );

          Promise.all(commentPromises).then((responses) => {
            this.comments = responses.map((res) => res.data);
          });
        });
    },
  },
};
</script>
