<template>
  <div class="post-detail">
    <h1>{{ post.title }}</h1>
    <p>By {{ post.by }} | {{ post.time }}</p>
    <p>{{ post.text }}</p>
    <a :href="post.url" target="_blank">View Original Post</a>
    <router-link :to="'/post/' + post.id + '/comments'"
      >View Comments</router-link
    >
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: ["id"],
  data() {
    return {
      post: {},
    };
  },
  mounted() {
    this.fetchPost();
  },
  methods: {
    fetchPost() {
      axios
        .get(
          `https://hacker-news.firebaseio.com/v0/item/${this.id}.json?print=pretty`
        )
        .then((response) => {
          this.post = response.data;
        });
    },
  },
};
</script>
