<template>
  <div class="home">
    <input v-model="searchQuery" placeholder="Search for posts" />
    <div v-for="post in filteredPosts" :key="post.id" class="post-item">
      <h3 @click="goToPost(post.id)">{{ post.title }}</h3>
      <p>{{ post.author }} | {{ post.score }} points</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      searchQuery: "",
      posts: [], // This will be populated by API data
    };
  },
  computed: {
    filteredPosts() {
      return this.posts.filter((post) =>
        post.title.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
  },
  mounted() {
    this.fetchPosts();
  },
  methods: {
    fetchPosts() {
      axios
        .get(
          "https://hacker-news.firebaseio.com/v0/topstories.json?print=pretty"
        )
        .then((response) => {
          const postIds = response.data;
          const postPromises = postIds
            .slice(0, 10)
            .map((id) =>
              axios.get(
                `https://hacker-news.firebaseio.com/v0/item/${id}.json?print=pretty`
              )
            );

          Promise.all(postPromises).then((responses) => {
            this.posts = responses.map((res) => res.data);
          });
        });
    },
    goToPost(id) {
      this.$router.push({ name: "post-detail", params: { id } });
    },
  },
};
</script>
